import { useParams } from "react-router";
import { apiStates, useApi } from "../components/useApi";

const AccesBlog = () => {
  let param = useParams();
  const { state, error, data } = useApi(
    `https://jsonplaceholder.typicode.com/posts/${param.id}`
  );

  switch (state) {
    case apiStates.ERROR:
      return <p>ERROR: {error || "General error"}</p>;
    case apiStates.SUCCESS:
      return (
        <>
          <p>{data.id}</p>
          <h1>{data.title}</h1>
          <p>{data.body}</p>
        </>
      );
    default:
      return <p>loading..</p>;
  }
};

export default AccesBlog;
